body{
	overflow: hidden;
	font-family: $mainFont;
	// background-image:url('./../img/site.jpg');
}
// GENERAL TITRES ______________
%title{
	margin: 0 0 1.5rem 0;
	color: $river;
	line-height: initial;
	font-weight: 100;
}
h1 {
	font-size: 3.4rem;
	line-height: 4rem;
}
h2 {
	font-size: 2.8rem;
	line-height: 3.4rem;
}
h3 {
	font-size: 2.4rem;
	line-height: 3rem;
}
h4 {
	font-size: 1.9rem;
	line-height: 2.5rem;
}

ul, ol {
	margin: 0 1.5rem 0 0;
}
ul li , p {
	font-size: $baseFontSize;
	line-height: 2.2rem;
	color: $stone;
	font-weight: 100;
}

p {
	margin-bottom: 1.5rem;
}

// Formatage des liens
a {
	color: $river;
	text-decoration: none;
	&:visited, &:hover{
		color: $ocean;
	}
}
a:not([class^="btn"]):hover {
	text-decoration: inherit;
}

// Formatage select
select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	cursor: pointer;
}
.select-style{
	position: absolute;
		right: 6rem;
		top: 6rem;
	.select-departement {
		z-index: 3;
		position: relative;
		padding: 0.5rem 5rem 0.7rem 1rem;
		border: 0.2rem solid $river;
		font-size: 1.6rem;
		font-weight: 600;
		color: $silver;
		text-transform: uppercase;
		// background-color: rgba(0,0,0,0);
		&:hover{
			color: $river;
		}
	}
}
.select-style:after{
	content: "";
	width: 4rem;
	height: 4rem;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 2;
	background-image:url('./../img/arrow-bottom.png');
	@include center-bg;
	border-left: 0.2rem solid $river;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	padding: 0.5rem 5rem 0.7rem 1rem;
	border: 0.2rem solid $river;
	font-size: 1.6rem;
	font-weight: 600;
	color: $silver;
	text-transform: uppercase;
	&:hover{
		color: $river;
	}
}
button, input[type="button"],
input[type="reset"],
input[type="submit"]{
	padding: 0.5rem 1rem 0.7rem;
	background-color: $river;
	box-shadow: none;
	color: #ffffff;
	text-shadow: none;
	text-transform: uppercase;
	font-size: 1.6rem;
	font-weight: 600;
	&:hover{
		box-shadow: none;
		background-color: $silver;
	}
}
input[type="submit"]{
	display: inline-block;
	padding: 1.1rem 1rem 1.1rem;
}
.wpcf7-form{
	input[type="text"],
	input[type="email"],
	input[type="url"],
	input[type="password"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="range"],
	input[type="date"],
	input[type="month"],
	input[type="week"],
	input[type="time"],
	input[type="datetime"],
	input[type="datetime-local"],
	input[type="color"],
	textarea {
		max-width: 42rem;
		width: 100%;
		margin-top: 0.6rem;
		padding: 0.5rem 1rem 0.7rem 1rem;
	}
	textarea {
		height: 12rem;
		max-height: 12rem;
	}
}

// Formulaires
button,
input,
select,
textarea{
	font-family: $mainFont;
}

// Formatage tableaux
th, td{
	vertical-align: top;
	text-align: center;
	color: $stone;
}
th{
	color: $storm;
}

a.btn-link {
	position: absolute;
	display: block;
	left: 0;
	width: auto;
	padding: 0.8rem 1.6rem;
	background-color: $ocean;
	font-family: $titleFont;
	color: white;
	transition: all 0.2s ease;
	&:after {
		content: "";
		position: absolute;
		height: 0.4rem;
		width: 100%;
		display: block;
		background-color: $deepSea;
		bottom: -0.4rem;
		left: 0;
		transition: all 0.2s ease;
	}
	&:hover {
		background-color: rgba($ocean,0.7);
		color: white;
		margin-top: 0.3rem;
		&:after {
			height: 0.2rem;
			bottom: -0.2rem;
		}
	}
}

// CONTENT ____________________________________________
.hentry {
	margin: 0;
}
.site-content{
	min-height: 90vh;
	// margin-top: 12rem;
}
.full{
	position: relative;
	max-width: 100%;
	margin: auto;
	padding: 10rem 2rem;
}
.block, .wrapper{
	position: relative;
	max-width: 98rem;
	margin: auto;
	padding: 5rem 2rem;
}


article {
	.entry-content {
		@include block;
		max-width: 98rem;
		width: 100%;
		margin: 8rem auto;
	}
}

.ipad {
	.presentation, .presentation-home, .bandeau-image {
		background-attachment: inherit;
	}
}

.presentation {
	position: relative;
	display: block;
	height: 54rem;
	min-height: 40rem;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	%title {
		position: absolute;
		bottom: 11rem;
		left: 50%;
		width: auto;
		transform: translateX(-50%);
		text-align: center;
		color: #ffffff;
		font-family: $titleFont;
		color: white;
		font-size: 5.5rem;
		line-height: 5rem;
		text-shadow: 0rem 0rem 0.5rem rgba(0,0,0,0.2);
		span {
			display: block;
			font-size: 5rem;
		}
	}
	.overlay {
		height: 100%;
		background-color: rgba(0,0,0,0.35);
	}
}


@font-face {
  font-family: 'custom-font';
  src:  url('../font/custom-font/custom-font.eot?dgkhhx');
  src:  url('../font/custom-font/custom-font.eot?dgkhhx#iefix') format('embedded-opentype'),
	url('../font/custom-font/custom-font.ttf?dgkhhx') format('truetype'),
	url('../font/custom-font/custom-font.woff?dgkhhx') format('woff'),
	url('../font/custom-font/custom-font.svg?dgkhhx#custom-font') format('svg');
  font-weight: normal;
  font-style: normal;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'custom-font' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.custom-font-facebook-square:before {
  content: "\f082";
}
.custom-font-phone:before {
  content: "\f095";
}
.custom-font-angle-left:before {
  content: "\f104";
}
.custom-font-angle-right:before {
  content: "\f105";
}
.custom-font-angle-up:before {
  content: "\f106";
}
.custom-font-angle-down:before {
  content: "\f107";
}

